import { useCallback,useContext } from "react";
import { loadFull } from "tsparticles";
import Particles from "react-tsparticles";
import NavContext from "../../NavContext";

export default function ParticlesBackground() {

    let {particleColor} = useContext(NavContext)
    
    
  const particlesInit = useCallback(async engine => {
    await loadFull(engine);
}, []);

const particlesLoaded = useCallback(async container => {
}, []);

return (
  <Particles 
  id="tsparticles"  
  init={particlesInit} 
  loaded={particlesLoaded}
  options={{
    fullScreen:{
      enable:true,
      zIndex:-1
    }
    ,
    background: {
        color: {
            value: "#000",
        },
    },
    fpsLimit: 120,
    interactivity: {
        events: {
            onClick: {
                enable: true,
                mode: "push",
            },
            onHover: {
                enable: true,
                mode: "repulse",
            },
            resize: true,
        },
        modes: {
            push: {
                quantity: 4,
            },
            repulse: {
                distance: 200,
                duration: 0.4,
            },
        },
    },
    particles: {
        color: {
            value:`${particleColor}`,
        },
        links: {
            color: "#ffffff",
            distance: 150,
            enable: false,
            opacity: 0.5,
            width: 1,
        },
        collisions: {
            enable: true,
        },
        move: {
            directions: "none",
            enable: true,
            outModes: {
                default:"bounce",
            },
            random: false,
            speed: 1,
            straight: false,
        },
        number: {
            density: {
                enable: true,
                area: 800,
            },
            value: 80,
        },
        opacity: {
            value: .5,
        },
        shape: {
            type:"circle",
        },
        size: {
            value: { min: 1, max: 5 },
        },
    },
    detectRetina: true,
}}
   />
);
}

